<template>
  <div class="account-info">
    <Crumbs
      :parent-manage="'资金管理'"
      :children-manage="'票据管理'"
      @isShowScreen="isShowScreen"
    ></Crumbs>

    <!-- 筛选内容 -->
    <Content
      v-if="isScreen"
      class="content screenStyle"
      :style="{padding: '24px', background: '#fff', marginBottom: '10px'}"
    >
      <Form
        :model="searchData"
        :label-width="85"
      >
        <Row>
          <Col span="11">
          <FormItem label="合同名称" width="100%">
            <Select
              v-model="searchData.contractid"
              transfer
              filterable
              clearable
            >
              <Option v-for="item in contractList" :key="item.contractId" :value="parseInt(item.contractId)">{{ item.contractName }}</Option>
            </Select>
          </FormItem>
          </Col>
          <Col span="11" offset="2">
          <FormItem label="往来单位" width="100%">
            <Input
              v-model="searchData.partyBName"
            ></Input>
          </FormItem>
          </Col>
        </Row>
        <!-- 提交开始和结束 -->
        <Row style="margin-top:15px;">
          <Col span="11">
          <FormItem label="日期开始时间">
            <DatePicker
              v-model="searchData.startTime"
              style="width:100%;"
              type="date"
              :options="startTimeOptions"
              placeholder="请选择日期开始时间"
              placement="bottom-start"
              @on-change="startTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
          <Col span="11" offset="2">
          <FormItem label="日期结束时间">
            <DatePicker
              v-model="searchData.endTime"
              style="width:100%;"
              type="date"
              :options="endTimeOptions"
              placeholder="请选择日期结束时间"
              placement="bottom-start"
              @on-change="endTimeChange"
            ></DatePicker>
          </FormItem>
          </Col>
        </Row>
        <Row type="flex" justify="end" style="margin-top:15px;">
          <Button type="primary" @click="searchList">确认</Button>
        </Row>
      </Form>
    </Content>
    <!-- /筛选内容 -->

    <!-- table-content -->
    <Content
      class="content"
      :style="{padding: '14px 24px', minHeight: '220px', background: '#fff'}"
    >
      <Button
        type="primary"
        icon="md-add"
        @click="handleAdd"
      >
        新增
      </Button>
      <Button
        type="primary"
        icon="ios-create"
        @click="handleModify"
      >
        修改
      </Button>
      <Button
        type="primary"
        icon="md-close"
        @click="handleDelete"
      >
        删除
      </Button>
      <Button icon="md-print" @click.stop="printContent(20)">
        打印
      </Button>
      <Button icon="md-exit" @click="excelExcel('/bill/exportBillListExcel')">
        导出
      </Button>
      <Table
        highlight-row
        class="table"
        :columns="columns"
        :data="billList"
        style="margin-top: 15px;"
        @on-current-change="selectRow"
      ></Table>
      <!-- 分页组件 -->
      <Page
        style="text-align: right"
        :total="total"
        :current="searchData.pageNo"
        :page-size="searchData.pageSize"
        show-sizer
        transfer
        @on-change="pageNoChange"
        @on-page-size-change="pageSizeChange"
      />
      <!-- 弹出框 -->
      <Modal
        v-model="popUp"
        class="selStyle specialWidth"
        :mask-closable="false"
      >
        <p slot="header">
          <span v-if="!isDisabled">请添加票据信息</span>
          <span v-else>票据信息</span>
        </p>
        <Tabs
          :animated="false"
          class="innerTabStyle"
          value="name1"
        >
          <TabPane
            label="基本信息"
            name="name1"
          >
            <Form
              ref="form"
              :model="form"
              :rules="ruleValidateBaseInfo"
              :label-width="80"
            >
              <Row>
                <Col span="12">
                <FormItem
                  label="合同名称"
                  prop="contractid"
                >
                  <Select
                    v-model="form.contractid"
                    :disabled="isDisabled"
                    transfer
                    filterable
                    clearable
                    placeholder="请选择合同名称"
                  >
                    <Option v-for="item in contractList" :key="item.contractId" :value="parseInt(item.contractId)">{{ item.contractName }}</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="乙方名称"
                  prop="partyBName"
                >
                  <Input
                    v-model="form.partyBName"
                    :disabled="isDisabled"
                    placeholder="请填写乙方名称"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="发票编号"
                  prop="code"
                >
                  <Input
                    v-model="form.code"
                    :disabled="isDisabled"
                    placeholder="请填写发票编号"
                  ></Input>
                </FormItem>
                </Col>
                <Col
                  span="12"
                >
                <FormItem
                  label="发票类别"
                  prop="type"
                >
                  <Select
                    v-model="form.type"
                    :disabled="isDisabled"
                    transfer
                    filterable
                    clearable
                    placeholder="请选择发票类别"
                  >
                    <Option v-for="(item,key) in typeList" :key="key" :value="parseInt(key)">{{ item }}</Option>
                  </Select>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="12">
                <FormItem
                  label="销售类别"
                  prop="sellType"
                >
                  <Select
                    v-model="form.sellType"
                    :disabled="isDisabled"
                    transfer
                    filterable
                    clearable
                    placeholder="请选择销售类别"
                  >
                    <Option v-for="(item,key) in sellList" :key="key" :value="parseInt(key)">{{ item }}</Option>
                  </Select>
                </FormItem>
                </Col>
                <Col span="12">
                <FormItem
                  label="发票金额"
                  prop="money"
                >
                  <Input
                    v-model="form.money"
                    :disabled="isDisabled"
                    placeholder="请填写发票金额"
                    number
                  ></Input>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem
                  label="开票日期"
                  prop="invoiceTime"
                >
                  <DatePicker v-model="form.invoiceTime" :disabled="isDisabled" type="date" placeholder="请选择开票日期" style="width:100%;" transfer clearable></DatePicker>
                </FormItem>
                </Col>
              </Row>
              <Row>
                <Col span="24">
                <FormItem label="备注">
                  <Input
                    v-model="form.remark"
                    :disabled="isDisabled"
                    type="textarea"
                    :autosize="{minRows: 1,maxRows: 5}"
                    placeholder="请填写备注"
                  ></Input>
                </FormItem>
                </Col>
              </Row>
            </Form>
          </TabPane>
          <TabPane
            label="发票内容"
            name="name8"
          >
            <!-- table-content -->
            <Content
              class="content"
              :style="{padding: '14px 24px',background: '#fff'}"
            >
              <Button
                v-if="!isDisabled"
                type="primary"
                icon="md-add"
                @click="billInfoAdd"
              >
                添加
              </Button>
              <Table
                highlight-row
                class="table"
                :columns="invoiceColumns"
                :data="billInfoList"
                style="margin-top: 15px;"
              >
                <template slot="name" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.name" type="text" />
                  <span v-else>{{ row.name }}</span>
                </template>
                <template slot="model" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.model" type="text" />
                  <span v-else>{{ row.model }}</span>
                </template>
                <template slot="danWei" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.danWei" type="text" />
                  <span v-else>{{ row.danWei }}</span>
                </template>
                <template slot="number" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.number" type="text" />
                  <span v-else>{{ row.number }}</span>
                </template>
                <template slot="hanShuiDanWei" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.hanShuiDanWei" type="text" />
                  <span v-else>{{ row.hanShuiDanWei }}</span>
                </template>
                <template slot="hanShuiMoney" slot-scope="{ row, index }">
                  <Input v-if="editIndex === index" v-model.trim="editForm.hanShuiMoney" type="text" />
                  <span v-else>{{ row.hanShuiMoney }}</span>
                </template>
                <template v-if="!isDisabled" slot="action" slot-scope="{ row, index }">
                  <div v-if="editIndex === index">
                    <Button type="text" @click="handleInlineSave()">
                      保存
                    </Button>
                    <Button type="text" @click="handleInlineDelete(row.id)">
                      删除
                    </Button>
                    <Button type="text" @click="editIndex = -1">
                      取消
                    </Button>
                  </div>
                  <div v-else>
                    <Button type="text" @click="handleInlineEdit(row, index)">
                      操作
                    </Button>
                  </div>
                </template>
              </Table>
            </Content>
          </TabPane>
          <TabPane
            label="附件"
            name="name7"
          >
            <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="{fileids: form.billFileids}" />
          </TabPane>
        </Tabs>
        <p
          slot="footer"
          style="text-align: left"
        >
          <Button icon="md-print">打印</Button>
          <Button icon="md-exit">导出</Button>
          <Button
            v-if="!isDisabled"
            style="float: right;margin-left: 8px"
            @click="popUp = false"
          >
            取消
          </Button>
          <Button
            v-if="!isDisabled"
            style="float: right"
            type="primary"
            @click="handleConfirm('form')"
          >
            确认
          </Button>
        </p>
      </Modal>
      <!-- /弹出框 -->
    </Content>

    <!-- 发票内容 -->
    <invoice-model ref="billModal" @refreshBillInfo="getBillInfoList"></invoice-model>
    <!--审核的弹出框-->
    <audit ref="audit" :form="auditForm" />
  </div>
</template>

<script>
import Crumbs from '../components/Crumbs';
import invoiceModel from './components/BillManage/invoice';
import uploadFile from '@/components/fileUpload.vue'
import tablePage from '@/mixins/tablePage.js';
import audit from '@/components/audit.vue'
export default {
  name: 'Accountinfo',
  components: { Crumbs, invoiceModel, uploadFile, audit },
  mixins: [tablePage],
  data() {
    return {
      isAdd: false,
      contractList: [], // 合同名称列表
      // 控制筛选框显示隐藏  默认隐藏
      isScreen: false,
      // 筛选内容_字段
      searchData: {
        // 合同名称
        contractId: '',
        // 往来单位（乙方名称）
        contactUnit: '',
        // 时间日期
        startTime: null,
        endTime: null,
        ascending: false,
        orderBy: '',
        pageNo: 1,
        pageSize: 10
      },
      editIndex: -1, // 当前聚焦的输入框的行数
      // 点击操作之后的文本框的转变
      editForm: {
        name: '',
        model: '',
        danWei: '',
        number: '',
        hanShuiDanWei: '',
        hanShuiMoney: '',
        operation: ''
      },
      // 表格内容 字段相对应
      billList: [],
      billInfoList: [],
      // 弹出框内容显示隐藏   默认隐藏
      popUp: false,
      // 添加表单的内容字段  ->  传递数据的字段
      form: {
        id: null,
        contractid: '',
        partyBName: '',
        code: '',
        invoiceType: '',
        sellType: '',
        money: null,
        invoiceTime: '',
        remark: ''
      },
      sellList: {
        0: '建筑工程服务',
        1: '机械设备购置',
        2: '其他'
      },
      typeList: {
        0: '普票',
        1: '专票'
      },
      // 表单内容的校验规则
      ruleValidateBaseInfo: {
        contractid: [
          { type: 'number', required: true, message: '请选择合同名称', trigger: 'change' }
        ],
        partyBName: [
          { required: true, message: '请填写乙方名称', trigger: 'blur' }
        ],
        code: [
          { required: true, message: '请填写发票编号', trigger: 'blur' }
        ],
        type: [
          { type: 'number', required: true, message: '请选择发票类别', trigger: 'change' }
        ],
        sellType: [
          { type: 'number', required: true, message: '请选择销售类别', trigger: 'change' }
        ],
        money: [
          { type: 'number', required: true, message: '请填写发票金额', trigger: 'blur' }
        ],
        invoiceTime: [
          { type: 'date', required: true, message: '请填写开票日期', trigger: 'blur' }
        ]
      },
      // 表格列
      columns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left',
          indexMethod: this.indexMethod
        },
        {
          title: '发票编号',
          key: 'code',
          render: (h, params) => {
            const row = params.row
            return h('span', {
              style: {
                color: '#2d8cf0',
                cursor: 'pointer'
              },
              on: {
                click: () => {
                  this.clearData()
                  const form = Object.assign({}, row)
                  form.invoiceTime = new Date(form.invoiceTime)
                  this.form = form
                  this.getBillInfoList()
                  this.$nextTick(() => {
                    this.$refs.uploadFile.getFileItems()
                    this.isDisabled = true;
                    this.popUp = true;
                  })
                }
              }
            }, row.code);
          }
        },
        {
          title: '开票日期',
          key: 'invoiceTime',
          align: 'center',
          render: (h, params) => {
            return h('div', this.$moment(params.row.invoiceTime).format('YYYY年MM月DD日'))
          }
        },
        {
          title: '发票金额',
          key: 'money'
        },
        {
          title: '发票类型',
          key: 'type',
          align: 'center',
          render: (h, params) => {
            return h('div', this.typeList[params.row.type])
          }
        },
        {
          title: '销售类型',
          key: 'sellType',
          align: 'center',
          render: (h, params) => {
            return h('div', this.sellList[params.row.sellType])
          }
        },
        {
          title: '往来单位',
          key: 'partyBName'
        },
        {
          title: '合同名称',
          key: 'contractName'
        },
        {
          align: 'center',
          title: '审核状态',
          key: 'auditStatus',
          fixed: 'right',
          width: 120,
          render: (h, params) => {
            return this.getAuditDetail(h, params)
          }
        }
      ],
      // 发票内容
      invoiceColumns: [
        {
          type: 'index',
          title: '序号',
          width: 60,
          align: 'center',
          fixed: 'left'
        },
        {
          title: '货物或应税劳务,服务名称',
          slot: 'name'
        },
        {
          title: '规格型号',
          slot: 'model'
        },
        {
          title: '单位',
          slot: 'danWei'
        },
        {
          title: '数量',
          slot: 'number'
        },
        {
          title: '含税单位',
          slot: 'hanShuiDanWei'
        },
        {
          title: '含税金额',
          slot: 'hanShuiMoney'
        },
        {
          align: 'center',
          title: '操作',
          slot: 'action',
          width: '130'
        }
      ],
      listUrl: '/bill/searchBillList'
    }
  },
  mounted() {
    // 获取合同名称 列表
    this.getContractList();
    this.searchList();
  },
  methods: {
    searchList() {
      const that = this;
      that.search(that.getNewSearchData(), (flag, res) => {
        if (flag) {
          const data = res.data
          that.billList = data.billList
          that.total = data.totalCount
        }
      })
    },
    // 获取合同信息列表
    getContractList() {
      const that = this
      that.$Spin.show();
      that.commonUtil.ajaxPostUtil('/contract/searchContractList', {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.contractList = res.data.contractList
        } else {
          that.$Message.error(res.msg);
        }
      }, () => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 子组件接收 控制筛选内容的隐藏和显示
    isShowScreen(msg) {
      this.isScreen = msg;
    },
    // 添加批复
    handleAdd() {
      this.clearData()
      this.isCreate = true;
      this.isDisabled = false;
      this.popUp = true;
    },
    // 更新操作
    handleModify() {
      if (!this.isSelected) {
        this.$Message.warning('您未选择需要修改的内容');
        return
      }
      this.clearData()
      const form = Object.assign({}, this.currentRow)
      // 未审核 和 审核驳回可以更改信息
      const flag = [2, 3].indexOf(form.auditStatus) !== -1
      form.invoiceTime = new Date(form.invoiceTime)
      this.form = form
      this.getBillInfoList()
      this.$nextTick(() => {
        this.$refs.uploadFile.getFileItems()
      })
      this.isCreate = false
      this.isDisabled = !flag;
      this.popUp = true;
    },
    clearData() {
      this.form = {
        id: null,
        contractid: '',
        partyBName: '',
        code: '',
        invoiceType: '',
        sellType: '',
        money: null,
        invoiceTime: '',
        remark: ''
      }
      this.isAdd = false
      this.$refs['form'].resetFields();
      const uploadFile = this.$refs.uploadFile
      if (uploadFile) {
        uploadFile.clearFileItems()
      }
    },
    // 打印操作
    handlePrint() { },
    // 导出操作
    handleExport() { },
    // 保存数据操作
    handleInlineSave() {
      const that = this;
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '修改数据中')
          ])
        }
      });
      that.commonUtil.ajaxPutUtil('/bill/updateBillInfo', that.editForm, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.$Message.success(res.msg);
          that.getBillInfoList()
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    handleInlineDelete(id) {
      const that = this;
      that.$Modal.confirm({
        title: '删除',
        content: '确认删除此数据吗？',
        onOk: () => {
          that.$Spin.show({
            render: (h) => {
              return h('div', [
                h('Icon', {
                  'class': 'demo-spin-icon-load',
                  props: {
                    type: 'ios-loading',
                    size: 18
                  }
                }),
                h('div', '加载中')
              ])
            }
          });
          that.commonUtil.ajaxDeleteUtil('/bill/deleteBillInfo/' + id, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.$Message.success(res.msg);
              that.getBillInfoList()
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            this.$Message.error('网络连接错误');
            this.$Spin.hide();
          }, that)
        }
      });
    },
    // 获取票据相关的发票信息
    getBillInfoList() {
      const that = this;
      that.editIndex = -1
      that.$Spin.show({
        render: (h) => {
          return h('div', [
            h('Icon', {
              'class': 'demo-spin-icon-load',
              props: {
                type: 'ios-loading',
                size: 18
              }
            }),
            h('div', '加载中')
          ])
        }
      });
      that.commonUtil.ajaxGetUtil('/bill/searchBillInfo/' + that.form.id, {}, (res) => {
        // 关闭数据加载
        that.$Spin.hide();
        if (res.code === 1) {
          that.billInfoList = res.data.billList
        } else {
          that.$Message.error(res.msg);
        }
      }, (error) => {
        that.$Message.error('网络连接错误');
        // 关闭数据加载
        that.$Spin.hide();
      }, that)
    },
    // 编辑表格
    handleInlineEdit(row, index) {
      this.editIndex = index;
      this.editForm = Object.assign({}, row)
    },
    // 发票内容的添加
    billInfoAdd() {
      const that = this;
      // 填写好票据的基本信息
      that.$refs.form.validate((valid) => {
        if (valid) {
          const id = that.form.id
          if (id) {
            that.$refs.billModal.handleModal(id, true)
          } else {
            // 调用接口，添加
            const form = that.getNewForm()
            that.$Spin.show({
              render: (h) => {
                return h('div', [
                  h('Icon', {
                    'class': 'demo-spin-icon-load',
                    props: {
                      type: 'ios-loading',
                      size: 18
                    }
                  }),
                  h('div', '加载中')
                ])
              }
            });
            that.commonUtil.ajaxPostUtil('/bill/addBill', form, (res) => {
              // 关闭数据加载
              that.$Spin.hide();
              if (res.code === 1) {
                that.isAdd = true
                that.form.id = res.data.id
                that.$refs.billModal.handleModal(res.data.id, true)
              } else {
                that.$Message.error(res.msg);
              }
            }, (error) => {
              that.$Message.error('网络连接错误');
              // 关闭数据加载
              that.$Spin.hide();
            }, that)
          }
        } else {
          that.$Message.error('请先填写票据的基本信息！');
        }
      })
    },
    // 删除
    handleDelete() {
      this.deleteData('/bill/deleteBill/', 'id')
    },
    getNewForm() {
      const form = Object.assign({}, this.form)
      form.invoiceTime = form.invoiceTime ? new Date(form.invoiceTime).getTime() : null
      form.fileIds = this.$refs.uploadFile.getFileIds()
      return form;
    },
    // 确认操作   关系后台数据
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          if (that.isAdd) {
            that.popUp = false;
            that.searchList()
          } else {
            const isCreate = that.isCreate
            const method = isCreate ? '/bill/addBill' : '/bill/updateBill'
            const form = that.getNewForm()
            that.submitForm(name, form, method, () => {
              that.popUp = false;
              that.searchList()
            })
          }
        } else {
          that.$Message.error('请填写好信息');
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.ivu-table .ivu-table-cell .demo-table-total td{
  text-align: center;
  color: red !important;
}
</style>



