<template>
  <div class="income">
    <Modal
      v-model="invoiceModel"
      class="selStyle specialWidth"
      :mask-closable="false"
    >
      <p slot="header">
        <span v-if="!isDisabled && !showTitle">请添加支出信息</span>
        <span v-else>支出信息</span>
      </p>
      <Tabs
        :animated="false"
        class="innerTabStyle"
        value="name1"
      >
        <TabPane
          label="信息设置"
          name="name1"
        >
          <Form
            ref="form"
            :model="form"
            :rules="ruleValidateType"
            :label-width="80"
          >
            <Row>
              <Col span="12">
              <FormItem
                label="名称"
                prop="name"
              >
                <Input
                  v-model.trim="form.name"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写货物或应税劳务,服务名称"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="规格型号"
                prop="model"
              >
                <Input
                  v-model.trim="form.model"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写规格型号"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="单位"
                prop="danWei"
              >
                <Input
                  v-model.trim="form.danWei"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写单位"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="数量"
                prop="number"
              >
                <Input
                  v-model.trim="form.number"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写数量"
                ></Input>
              </FormItem>
              </Col>
            </Row>
            <Row>
              <Col span="12">
              <FormItem
                label="含税单位"
                prop="hanShuiDanWei"
              >
                <Input
                  v-model.trim="form.hanShuiDanWei"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写含税单位"
                ></Input>
              </FormItem>
              </Col>
              <Col span="12">
              <FormItem
                label="含税金额"
                prop="hanShuiMoney"
              >
                <Input
                  v-model.trim="form.hanShuiMoney"
                  :disabled="isDisabled"
                  clearable
                  placeholder="请填写含税金额"
                ></Input>
              </FormItem>
              </Col>
            </Row>
          </Form>
        </TabPane>
        <TabPane
          label="附件"
          name="name7"
        >
          <Icon
            v-if="!isDisabled"
            size="16"
            type="md-alert"
          />
          <uploadFile ref="uploadFile" :is-disabled="isDisabled" :form="form" />
        </TabPane>
      </Tabs>
      <p
        slot="footer"
        style="text-align: left"
      >
        <Button icon="md-print">打印</Button>
        <Button icon="md-exit">导出</Button>
        <Button
          v-if="!isDisabled"
          style="float: right;margin-left: 8px"
          @click="handleModal(null, false)"
        >
          取消
        </Button>
        <Button
          v-if="!isDisabled"
          style="float: right"
          type="primary"
          @click="handleConfirm('form')"
        >
          确认
        </Button>
      </p>
    </Modal>
  </div>
</template>

<script>
import uploadFile from '@/components/fileUpload.vue'
export default {
  name: 'InvoiceModel',
  components: {
    uploadFile
  },
  props: {
    isDisabled: {
      type: Boolean,
      value: false
    },
    showTitle: {
      type: Boolean,
      value: false
    }
  },
  data() {
    return {
      form: {
        name: '',
        model: '',
        danWei: '',
        number: '',
        hanShuiDanWei: '',
        hanShuiMoney: ''
      },
      invoiceModel: false,
      ruleValidateType: {
        name: [
          { required: true, message: '请填写货物或应税劳务', trigger: 'blur' }
        ],
        model: [
          { required: true, message: '请填写规格型号', trigger: 'blur' }
        ],
        danWei: [
          { required: true, message: '请填写单位', trigger: 'blur' }
        ],
        number: [
          { required: true, message: '请填写数量', trigger: 'blur' }
        ],
        hanShuiDanWei: [
          { required: true, message: '请填写含税单位', trigger: 'blur' }
        ],
        hanShuiMoney: [
          { required: true, message: '请填写含税金额', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    handleModal(billid, flag) {
      this.form = {
        billid: billid || null,
        name: '',
        model: '',
        danWei: '',
        number: '',
        hanShuiDanWei: '',
        hanShuiMoney: ''
      }
      this.$refs.form.resetFields();
      this.invoiceModel = flag
    },
    handleConfirm(name) {
      const that = this;
      that.$refs[name].validate((valid) => {
        if (valid) {
          that.$Spin.show();
          that.commonUtil.ajaxPostUtil('/bill/addBillInfo', that.form, (res) => {
            // 关闭数据加载
            that.$Spin.hide();
            if (res.code === 1) {
              that.handleModal(null, false)
              that.$emit('refreshBillInfo')
            } else {
              that.$Message.error(res.msg);
            }
          }, (error) => {
            that.$Message.error('网络连接错误');
            // 关闭数据加载
            that.$Spin.hide();
          }, that)
        } else {
          that.$Message.error('请填写好发票信息');
        }
      })
    }
  }
}
</script>




